import React from 'react';
import { rerenderEntireTree } from "../render";
import {Header,InfoRow, Progress, Panel, PanelHeader, Group, Button, Div, FormLayout, FormLayoutGroup, Select } from '@vkontakte/vkui';



const PanelStart = (props) => {
    props.formData.progress = 0;

    //получаем значение
    const handleTargetCodeChange = e => {
        let select = e.currentTarget;

        if (select.value !== '') {
            props.formData.targetCode = select.value;
            // props.formData.progress = 20;
            rerenderEntireTree(props.formData);

        } else {
            // props.formData.progress = 0;
            props.formData.targetCode = '';
            rerenderEntireTree(props.formData);
        }

    };

    return (

        <Panel id={props.id}>
            <PanelHeader>Цель получения пропуска</PanelHeader>
            <Group>
                <Div>
                    <InfoRow header="Прогресс">
                        <Progress value={props.formData.progress} />
                    </InfoRow>
                </Div>
                <FormLayout>

                    <FormLayoutGroup top="Цель получения пропуска:">
                        <Select placeholder="..."
                            onChange={handleTargetCodeChange}
                            defaultValue={props.formData.targetCode}>
                            <option value="1">Цифровой пропуск для работающих</option>
                            <option value="2">Разовая поездка для посещения медицинской организации</option>
                            <option value="3">Разовая поездка в иных целях</option>
                        </Select>
                    </FormLayoutGroup>
                </FormLayout>
                <Div>
                    <Button size="xl" level="2" onClick={() => props.go('panelDocument')} id='nextBtn' >
                        Дальше
				</Button>
                </Div>
                
            </Group>
            <Group
          header={<Header mode="secondary">info:</Header>}
          description="Приложение не собирает, введенных пользователем данных
            В настоящий момент валидация полей отсутствует,  вводите данные верно.
          Если будет необходимость, мы добавим возможность сохранять веденные вами данные на вашем устройстве
          Для подачи заявки на получение цифрового пропуска, вам необходимо отправить сгенерированное сообщение  помощью СМС на номер 7377."
          separator="show"
        >
        </Group>
        </Panel>
    );
}

export default PanelStart;